body {
  background-color: #ebe8e8;
  color: white;
  overflow: hidden;
}



h1 {
  text-align: center;
  color: #333; /* Dark color for contrast */
  font-family: 'Roboto', sans-serif; /* Modern, clean font */
  font-size: 3rem; /* Large font size for prominence */
  font-weight: 600; /* Extra bold for impact */
  margin: 10px 0; /* Margin for spacing */
  position: relative;
  
  background: linear-gradient(135deg, #112bd6, #04011e); /* Gradient background */
  color: transparent; /* Hide text color to reveal gradient */
  -webkit-background-clip: text; /* Clip gradient to text */
  background-clip: text;
  text-transform: uppercase; /* Uppercase for a modern touch */
  letter-spacing: 1px; /* Spacing between letters */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3); /* Subtle shadow for 3D effect */
  transition: all 0.3s ease;
}

.todo-wrapper {
  background: #353434;
  padding: 2%;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0px 5px 10px rgb(27, 27, 27);
  border-radius: 10px;
}

.todo-input {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid gray;
  padding-bottom: 25px;
  margin-bottom: 25px;
}

.todo-input-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 25px;
}

.todo-input-item label {
  font-weight: bold;
  margin-bottom: 10px;
}

.todo-input-item input {
  padding: 8px;
  border: none;
  width: 250px;
  transition: border-color 0.3s ease;
  font-size: 16px;
  transition: all 0.3s ease;
  background: #fefefe; /* Light background for input */
  color: #353434; /* Dark text color for better contrast */
}

.todo-input-item input:focus {
  outline: 2px solid rgb(0, 230, 122);
}

.primaryBtn, .secondaryBtn {
  border: none;
  border-radius: 20px; /* Rounded corners for a soft, candy-like appearance */
  margin-top: 25px;
  padding: 10px 20px; /* More padding for a better look */
  cursor: pointer;
  position: relative;
  overflow: hidden;
  font-weight: bold;
  color: white;
  transition: all 0.3s ease;
}

.primaryBtn {
  background: linear-gradient(45deg, #ff6b6b, #ff9a9e); /* Candy-like gradient */
  box-shadow: 0px 5px 15px rgba(255, 105, 135, 0.5); /* Soft shadow */

}

.primaryBtn:hover {
  background: linear-gradient(45deg, #ff9a9e, #ff6b6b); /* Reversed gradient on hover */
  transform: translatey(3px); /* Lift effect */
  box-shadow: 0px 8px 20px rgba(255, 105, 135, 0.7); /* More pronounced shadow on hover */
}

.secondaryBtn {
  background: linear-gradient(45deg, #28f112, #6f86d6); /* Candy-like gradient */
  box-shadow: 0px 5px 15px rgba(72, 198, 239, 0.5); /* Soft shadow */
  border-radius:10px;
  margin-bottom:30px;
  margin-right:10px;
  
}

.secondaryBtn:hover {
  background: linear-gradient(45deg, #6f86d6, #48c6ef); /* Reversed gradient on hover */
  transform: translateY(-3px); /* Lift effect */
  box-shadow: 0px 8px 20px rgba(72, 198, 239, 0.7); /* More pronounced shadow on hover */
}

.active {
  background-color: rgb(4, 183, 99);
}

.todo-list {
  display: flex;
  flex-direction: column;
}

.todo-list-item {
  background-color: #414040;
  display: flex;
  justify-content: space-between;
  padding: 25px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  align-items: center;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.todo-list-item:hover {
  background-color: #515151;
  transform: scale(1.02);
}

.todo-list-item h3 {
  font-size: 25px;
  color: rgb(0, 230, 122);
  font-weight: bold;
  margin: 0;
}

.icon {
  font-size: 30px;
  cursor: pointer;
  color: red;
  transition: color 0.3s ease;
}

.icon:hover {
  color: rgb(231, 231, 231);
  
}

.check-icon {
  font-size: 25px;
  margin-left: 10px;
  color: rgb(53, 140, 247);
  transition: color 0.3s ease, transform 0.3s ease;
}

.check-icon:hover {
  color: rgb(0, 230, 122);
  transform: scale(1.2);
}
.edit__wrapper{
  background-color: #414040;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.edit__wrapper input,.edit__wrapper textarea{
  border: 1px solid lightgray;
  padding: 10px;
  margin: 5px;
  border-radius: 3px;
}

.edit__wrapper .primaryBtn{
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  font-size: 16px;
  width:fit-content;
}
.search-wrapper {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  align-items: center; /* Align items vertically */
}

.search-input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 200px;
  margin-left: 10px; /* Add space between the icon and input */
}

.search-icon {
  font-size: 20px; /* Adjust size as needed */
  color: #f4f3f3; /* Adjust color as needed */
}
@media only screen and (max-width:600px) 
{
  h1 {
    font-size:2.2rem; /* Smaller font size for smaller screens */
    padding:20px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
  }

  .todo-list-item {
    flex-direction: column;
    align-items: flex-start; /* Align items vertically on small screens */
  }

  .todo-list-item div {
    width: 100%; /* Make todo item div full width */
  }
  .todo-wrapper{
    text-align:center;
    justify-content: center;
    margin-top:40px;
   height:450px;

  }
  .search-input {
    width:40%;
    margin-left: 0;
  }

  .secondaryBtn {
    width:auto; /* Make buttons full width on very small screens */
    margin-right: 10px;
    margin-bottom: 18px; /* Add margin between buttons */
    
  }

  .edit__wrapper input,
  .edit__wrapper textarea {
    width: 100%; /* Make inputs full width on small screens */
  }
  .search-icon
  {
    margin-right:10px;
  }
  .todo-input-item label {
    font-weight: bold;
    margin-bottom: 10px;
    font-size:20px;
  }
 .secondaryBtn{
  margin-top:0px;
 }
  
.todo-input{
  
  display:grid;
  width:300px;
  
  border-bottom: 1px solid gray;
  padding-bottom: 25px;
  margin-bottom: 25px;
}
    
}